body {
  width: 100%;
  height: 100%;
  padding: 10px;
  position: relative;
}

.app {
  width: 70%;
  padding: 20px;
  margin: auto;
  border: 1px solid #000;
  box-sizing: border-box;
}

input {
  width: 100%;
  height: 30px;
}

.add {
  margin-top: 20px;
  background-color: #000;
  color: #fff;
  border: 0;
  box-sizing: border-box;
  width: 100%;
  padding: 15px;
}

.list {
  padding-top: 20px;
}

.item {
  width: 100%;
  border-bottom: 1px solid #000;
  margin-bottom: 10px;
  padding-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.remove {
  border: 0;
  background-color: rgb(212, 101, 101);
  color: #fff;
  padding: 10px;
}

.footer {
  width: 200px;
  position: absolute;
  bottom: -20px;
  right: 0;
  left: 0;
  margin: auto;
}

@media screen and (max-width: 400px) {
  .app {
    width: 100%;
  }
}